* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

h2 {
  margin-top: 10px;
  margin-bottom: 10px;
  margin-right: 5px;
  font-size: 26px;
}
h1 {
  margin-top: 10px;
  margin-bottom: 10px;
  margin-left: 10px;
}
#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.country-list {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  list-style: none;
}
.country-list img {
  display: block;
  margin-right: 10px;
}
.country-list span {
  font-size: 24px;
}
.country-info {
  padding-left: 20px;
}
.item {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  list-style: none;
  align-items: baseline;
  font-size: 24px;
}
